import React from 'react';

const LocationMap = ({ latitude = -0.342304, longitude = 31.737461 }) => {
  // Construct the Google Maps embed URL
  const locationUrl = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;

  // Construct a descriptive title for SEO and screen readers
  const mapTitle = `Map showing ADPHA Uganda office location at latitude ${latitude} and longitude ${longitude}`;

  return (
    <iframe
      src={locationUrl}
      width="100%"
      height="450"
      style={{ border: 0 }}
      allowFullScreen=""
      loading="lazy"
      title={mapTitle}
      aria-label={mapTitle}
    ></iframe>
  );
};

export default LocationMap;